.custom-input-wrapper {
  height: 60px;
  position: relative;
}

.custom-input-wrapper:not(:last-child) {
  margin-bottom: 18px;
}

.custom-input-wrapper .custom-input-input {
  height: calc(3.5rem + 2px);
  width: 100%;
  color: #004d40;
  border: 1px solid #0f713e;
  border-radius: 8px;
  outline: none;
  padding: 10px 14px;
  font-family: Lato, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.18;
}

.custom-input-wrapper .custom-input-label {
  height: 100%;
  pointer-events: none;
  transform-origin: 0 0;
  color: #004d40;
  border: 1px solid rgba(0, 0, 0, 0);
  padding: 10px 14px;
  font-family: Lato, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.18;
  transition: opacity .15s ease-in-out, transform .15s ease-in-out;
  position: absolute;
  top: 10px;
  left: 0;
}

.custom-input-wrapper .custom-input-input::-ms-input-placeholder {
  color: rgba(0, 0, 0, 0);
}

.custom-input-wrapper .custom-input-input::placeholder {
  color: rgba(0, 0, 0, 0);
}

.custom-input-wrapper .custom-input-input:focus, .custom-input-wrapper .custom-input-input:not(:placeholder-shown) {
  background: #fdfbf9;
  border: 1px solid #3c7d7a;
  border-radius: 8px;
  padding-top: 10px;
  padding-bottom: 8px;
  box-shadow: 0 0 0 2px #07e19a;
}

.custom-input-wrapper .custom-input-input:focus ~ .custom-input-label, .custom-input-wrapper .custom-input-input:not(:placeholder-shown) ~ .custom-input-label {
  opacity: .6;
  padding-top: 10px;
  padding-bottom: 8px;
  font-size: 13px;
  line-height: 1.23;
  transform: scale(.85)translateY(-.75rem)translateX(.15rem);
}

